
import Xheader from '@/components/comother/rheader'
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { Popup } from 'mint-ui';
Vue.component(Popup.name, Popup);
export default {
data(){
let list = [{name:'王俊凯',btn:'去处理'},{name:'李琳',btn:'去处理'},{name:'潘旭',btn:'去处理'},{name:'赵杨',btn:'待审批'}];
for(let i=0;i<list.length;i++){
if(list[i].btn == '待审批'){
list[i].isYellow = true;
}
else{
list[i].isYellow = false;

}
}
return {
contents:{rights:'',title:'支部党员'},
list:list,
isYellow:false,
popupVisible:false,
showPop:false
}
},
components:{
'r-header':Xheader,
ViewBox
},
directives:{
TransferDom
},
methods:{
changeItem(){
this.popupVisible = true
},

know(){
this.popupVisible = false
}
},
mounted(){

}
}



